.front {
  background-image: linear-gradient(
      to right bottom,
      rgba(180, 201, 176, 0.801),
      #42554f
    ),
    url("../../images/happy-nurse.jpg");
  background-size: cover;
  background-position: center;
  top: 0;
  position: relative;
  width: auto;
  height: 80vh;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 95%, 0 100%);
}

.text-box {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  overflow: hidden;
}

.primary {
  display: block;
  font-size: 7vw;
  font-weight: 400;
  letter-spacing: 4px;

  animation-name: moveDown;
  animation-duration: 2s;
}

.secondary {
  display: block;
  font-size: 3vw;
  font-weight: 300;
  letter-spacing: 2px;
  text-align: center;

  animation-name: moveUp;
  animation-duration: 2s;
}

@keyframes moveDown {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }

  70% {
    transform: translateY(15px)
  }

  100% {
    opacity: 1;
    transform: translate(0)
  }
}

@keyframes moveUp {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  70% {
    transform: translateY(-15px)
  }

  100% {
    opacity: 1;
    transform: translate(0)
  }
}