.nav {
    position: relative;
    padding: inherit;
}

.nav ul {
    display: flex;
    flex-direction: column;
    position: fixed;
    z-index: 1;
    background: white;
    padding: inherit;
    width: calc(100vw - 3.5rem);
    max-width: 100%;
}

.nav ul li {
    list-style: none;
    height: 5rem;
    font-size: 20px;
    letter-spacing: 2px;
    line-height: 70px;
    cursor: pointer;
    width: auto;
    padding: inherit;
    overflow-y: hidden;
}

.nav ul:last-child {
    padding-bottom: 25px;
}

.nav ul li:hover {
    color: #ac0e77;
    font-size: 25px;
}